body {
  box-sizing: border-box;
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.search-form {
  text-align: center;
  margin: 20px;
}

.search-form input {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 12px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 12px 20px;
  font-size: 16px;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  flex-direction: column;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  border-bottom: 1px solid #ddd;
}

.info {
  background-color: #fff;
  flex-direction: column;
  flex: 1;
  padding: 10px;
  display: flex;
}

.info p {
  color: #555;
  margin: 0;
  font-size: 14px;
}

.info-item {
  color: #333;
  align-items: center;
  margin-right: 10px;
  font-weight: bold;
  display: flex;
}

.info-item i {
  color: #4caf50;
  margin-right: 8px;
  font-size: 18px;
}

.load-more {
  text-align: center;
  cursor: pointer;
  color: #2ecc71;
  background-color: #fff;
  border: 2px solid #2ecc71;
  border-radius: 5px;
  outline: none;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: bold;
  text-decoration: none;
  transition: background-color .3s, color .3s, border .3s;
  display: inline-block;
}

.load-more:hover {
  color: #fff;
  background-color: #2ecc71;
  border: 2px solid #fff;
}

.box-btn {
  justify-content: center;
  display: flex;
}

